<template>
  <section class="section section-color-gray">
    <div class="section-header">
      <h2 class="section-heading-eyebrow">Projets</h2>
      <h3 class="section-heading">Selection de projets réalisés.</h3>
    </div>
    <div class="container container-large">
      <div class="layout-grid intro-grid">
        <ProductList2
          :products="products"
          :viewer-open="appearedDialog"
          @showDialog="showDialog"
        />
      </div>
        <Viewer2
          :full-width="800"
          :full-height="500"
          :product="product"
          :appearedDialog="appearedDialog"
          @hideDialog="hideDialog"
        />
    </div>
  </section>
</template>

<script>
import ProductList2 from './ProductList2'
import Viewer2 from './Viewer2'
export default {
  name: 'Products2',
  components: {
    ProductList2,
    Viewer2
  },
  data () {
    return {
      loaded: false,
      appearedDialog: false,
      product: {
        id: ''
      },
      products: [
        {
          id: '1',
          title: 'Bootstrap Template',
          thumb: require('../assets/img/projects/lea/lea_template.jpg'),
          full: require('../assets/img/projects/lea/lea_template.jpg'),
          carousel: [
            require('../assets/img/projects/lea/lea_img_01.jpg'),
            require('../assets/img/projects/lea/lea_img_02.jpg'),
            require('../assets/img/projects/lea/lea_img_03.jpg'),
            require('../assets/img/projects/lea/lea_img_04.jpg'),
            require('../assets/img/projects/lea/lea_img_05.jpg'),
            require('../assets/img/projects/lea/lea_img_06.jpg'),
            require('../assets/img/projects/lea/lea_img_07.jpg')
          ]
        },
        {
          id: '2',
          thumb: require('../assets/img/projects/teo/teo_template.jpg'),
          full: require('../assets/img/projects/teo/teo_img_01.jpg'),
          carousel: [
            require('../assets/img/projects/teo/teo_img_01.jpg'),
            require('../assets/img/projects/teo/teo_img_02.jpg'),
            require('../assets/img/projects/teo/teo_img_03.jpg'),
            require('../assets/img/projects/teo/teo_img_04.jpg'),
            require('../assets/img/projects/teo/teo_img_05.jpg'),
            require('../assets/img/projects/teo/teo_img_06.jpg'),
            require('../assets/img/projects/teo/teo_img_07.jpg'),
            require('../assets/img/projects/teo/teo_img_08.jpg')
          ]
        }
      ]
    }
  },
  computed: {
  },
  methods: {
    showDialog (prod) {
      this.product = prod
      this.appearedDialog = true
    },
    hideDialog () {
      this.appearedDialog = false
    }
  },
  mounted () {
  }
}
</script>

<style scoped>
</style>



