<template>
  <section class="section section-color-gray">
    <div class="section-header">
      <h2 class="section-heading-eyebrow">Processus</h2>
      <h3 class="section-heading">
        Mon plan de travail<br />pour créer votre site web.
      </h3>
    </div>
    <div class="container container-large">
        <div class="wrapper">
          <div class="card fade-in translate">
            <h2><span>Étape 1</span>Demande de Projet</h2>
            <p>
              Au début de notre collaboration se trouve la demande de projet.
              Vos informations m'aident à comprendre si je suis la bonne personne pour votre projet.
              Si oui, nous organiserons un rendez-vous pour que je puisse en savoir plus.
            </p>
          </div>
          <div class="card fade-in translate">
            <h2><span>Étape 2</span>Première Rencontre</h2>
            <p>
              Une confiance mutuelle doit être installée pour que nous puissions travailler ensemble.
              Lors de notre première rencontre, nous apprendrons à nous connaître et à voir si nous sommes bien adaptés.
              Nous discutons également des objectifs et des exigences exacts, clarifions les questions et définissons les prochaines étapes.
            </p>
          </div>
          <div class="card fade-in translate">
            <h2><span>Étape 3</span>Proposition</h2>
            <p>
              Après en avoir appris le plus possible sur votre projet, je crée une proposition individuelle.
              Je ne facture pas mon travail à l'heure, mais par projet.
              De cette façon, vous avez un aperçu complet des coûts.
            </p>
          </div>
          <div class="card fade-in translate">
            <h2><span>Étape 4</span>Mise en Oeuvre</h2>
            <p>
              Après acceptation de ma proposition, une maquette grossière du fonctionnemlent sera réalisée et validée par vos soins.
              Dès lors que j'ai en ma possession tous les outils nécessaires, je commence le développement de votre site.
              Il est important de pouvoir resté en contact et ainsi preservé une certaine transparence sur votre projet.
            </p>
          </div>
          <div class="card fade-in translate">
            <h2><span>Étape 5</span>Mise en Ligne</h2>
            <p>
              Avant le lancement, le site Web fini passe par une assurance qualité interne au cours de laquelle il sera mis à l'épreuve.
              Respect des normes de codage, adaptation du site sur différents périphériques etc...
              L'hébergement du site est à votre charge mais je pourrais vous aider à bien le choisir.
            </p>
          </div>
        </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'Process',
  methods: {
    appearCard () {
      // Select all classes fade-in on card
      const faders = document.querySelectorAll('.fade-in')
      // Options for card enter in viewport
      const appearOptions = {
        threshold: 1,
        rootMargin: '0px 0px 170px 0px'
      }
      // Start Observer
      const appearOnScroll = new IntersectionObserver(function (entries, appearOnScroll) {
        entries.forEach(entry => {
          if (!entry.isIntersecting) {
            entry.target.classList.remove('appear')
          } else {
            entry.target.classList.add('appear')
            appearOnScroll.unobserve(entry.target)
          }
        })
      },
      appearOptions)
      // Start Effect card on scroll down
      faders.forEach(fader => {
        appearOnScroll.observe(fader)
      })
    },
    onCheckPoint () {
      // Select all classes translate on card
      const translates = document.querySelectorAll('.translate')
      // Options for card enter in viewport
      const checkPointOptions = {
        threshold: 1,
        root: document.querySelector('card'),
        rootMargin: '0px 0px -230px 0px'
      }

      const transOnCheck = new IntersectionObserver(function (entries, transOnCheck) {
        entries.forEach(entry => {
          if (!entry.isIntersecting) {
            entry.target.classList.remove('check')
          } else {
            entry.target.classList.add('check')
            transOnCheck.unobserve(entry.target)
          }
        })
      },
      checkPointOptions)

      translates.forEach(translate => {
        transOnCheck.observe(translate)
      })
    },
    handleScroll () {
      this.appearCard()
      this.onCheckPoint()
    }
  },
  mounted () {
    document.addEventListener('scroll', this.handleScroll)
  },
  destroyed () {
    document.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style scoped>
.wrapper {
  margin-top: 100px;
  padding-top: 100px;
  padding-bottom: 1000px;
  max-width: 700px;
  margin: 0 auto;
}

.card {
  position: sticky;
  border: 1px solid #ccc;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.4);
  background-color: #fefefe;
  color: #333;
  padding: 60px;
  border-radius: 28px;
  margin-bottom: 170px;
  transition: all 300ms ease-in;
}
.card h2,
.card span {
  padding-bottom: 10px;
}
.card span {
  display: block;
  font-size: 14px;
  color: #0fa7ff;
}
.card:first-child {
  top: 300px;
}
.card:nth-child(1) {
  top: 320px;
}
.card:nth-child(2) {
  top: 340px;
}
.card:nth-child(3) {
  top: 360px;
}
.card:nth-child(4) {
  top: 380px;
}
.fade-in {
  opacity: 0;
}
.fade-in.appear {
  opacity: 1;
}
.translate {
  transform: translate(0px, 0px) scale(0.95, 0.95);
}
.translate.check {
  transform: translate(0px, -40px) scale(1, 1);
}
</style>



