<template>
  <div class="preview-grid">
    <div class="card-wrap"
    v-for="prod in products"
    :key="prod.id"
    @click="showDialog(prod)"
    >
      <button class="card">
        <div class="card-bg">
          <img class="image-dialog-thumb" ref="thumb" :src="prod.thumb" />
        </div>
      </button>
    </div>
  </div>
</template>
<script>
export default {
  name: 'ProductList2',
  props: {
    products: {
      type: Array,
      required: true,
      carousel: {
        type: Array,
        required: true
      }
    },
    viewerOpen: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  data () {
    return {
      openProductId: null
    }
  },
  methods: {
    showDialog (product) {
      this.openProductId = product.id
      this.$emit('showDialog', product)
    }
  },
  mounted () {
  }
}
</script>

<style scoped>
</style>



