<template>
<div class="container p-5">
  <div id="exampleModal">
    <transition @enter="startTransitionModal" @after-enter="endTransitionModal" @before-leave="endTransitionModal" @after-leave="startTransitionModal">
      <div class="modal fade" v-show="appearedDialog" @click="hideDialog" ref="modal">
        <div class="modal-dialog animate__zoomInDown">
          <div class="modal-content">
            <div class="modal-header">
              <img :src="appearedDialog &amp;&amp; product.full" :width="fullWidth" :height="fullHeight" @load="onLoadFull"/>
              <button type="button" class="close" @click="hideDialog" data-dismiss="modal">
                <span>&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <h1 class="modal-title">{{ product.title }}</h1>
            </div>
            <div class="modal-footer">
              <div class="carousel slide" data-ride="carousel">
                <div class="carousel-inner">
                  <div class="carousel-item" v-for="(image, index) in product.carousel" :key="image.id" :class="{ 'active': index === 0 }">
                    <img class="d-block w-100" :src="image" alt="">
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <div class="modal-backdrop fade d-none" ref="backdrop"></div>
  </div>
</div>
</template>
<script>
export default {
  name: 'Viewer2',
  props: {
    product: {
      type: Object,
      required: true,
      carousel: {
        type: Object,
        required: true
      }
    },
    fullWidth: Number,
    fullHeight: Number,
    appearedDialog: Boolean
  },
  data () {
    return {
      loaded: false
    }
  },
  methods: {
    hideDialog () {
      this.$emit('hideDialog')
      document.body.style.overflow = 'auto'
    },
    onLoadFull () {
      this.loaded = true
    },
    startTransitionModal () {
      this.$refs.backdrop.classList.toggle('d-block')
      this.$refs.modal.classList.toggle('d-block')
    },
    endTransitionModal () {
      document.body.style.overflow = 'hidden'
      this.$refs.backdrop.classList.toggle('show')
      this.$refs.modal.classList.toggle('show')
    }
  },
  mounted () {
  }
}
</script>

<style scoped>
.modal {
  overflow-x: hidden;
  overflow-y: auto;
}
.modal-dialog {
  max-width: 800px;
}
.modal-content {
  margin: 0;
  padding: 0;
  background-color: #FEFEFE;
  border-radius: 28px 28px 0 0;
}
.modal-header {
  padding: 0;
  border-radius: 28px 28px 0 0;
  overflow: hidden;
}
.modal .close {
  position: absolute;
  right: 30px !important;
  top: 30px !important;
  width: 2.5rem;
  height: 2.5rem;
  background-color: rgba(193, 193, 193, .9);
  border-radius: 50%;
  font-size: 1.8rem;
  padding: 0;
}
.modal-footer {
  padding: 0;
  border: none;
}
.modal-footer > * {
  margin: 0;
}
@-webkit-keyframes zoomInDown {
from {
opacity: 0;
-webkit-transform: scale3d(0.1, 0.1, 0.1) translate3d(0, -1000px, 0);
transform: scale3d(0.1, 0.1, 0.1) translate3d(0, -1000px, 0);
-webkit-animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
}
60% {
opacity: 1;
-webkit-transform: scale3d(0.475, 0.475, 0.475) translate3d(0, 60px, 0);
transform: scale3d(0.475, 0.475, 0.475) translate3d(0, 60px, 0);
-webkit-animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1);
animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1);
}
}
@keyframes zoomInDown {
from {
opacity: 0;
-webkit-transform: scale3d(0.1, 0.1, 0.1) translate3d(0, -1000px, 0);
transform: scale3d(0.1, 0.1, 0.1) translate3d(0, -1000px, 0);
-webkit-animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
}
60% {
opacity: 1;
-webkit-transform: scale3d(0.475, 0.475, 0.475) translate3d(0, 60px, 0);
transform: scale3d(0.475, 0.475, 0.475) translate3d(0, 60px, 0);
-webkit-animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1);
animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1);
}
}
.animate__zoomInDown {
-webkit-animation-name: zoomInDown;
animation-name: zoomInDown;
-webkit-animation-duration: 1s;
animation-duration: 1s;
-webkit-animation-fill-mode: both;
animation-fill-mode: both;
}

@-webkit-keyframes backInDown {
0% {
-webkit-transform: translateY(-1200px) scale(0.7);
transform: translateY(-1200px) scale(0.7);
opacity: 0.7;
}
80% {
-webkit-transform: translateY(0px) scale(0.7);
transform: translateY(0px) scale(0.7);
opacity: 0.7;
}
100% {
-webkit-transform: scale(1);
transform: scale(1);
opacity: 1;
}
}
@keyframes backInDown {
0% {
-webkit-transform: translateY(-1200px) scale(0.7);
transform: translateY(-1200px) scale(0.7);
opacity: 0.7;
}
80% {
-webkit-transform: translateY(0px) scale(0.7);
transform: translateY(0px) scale(0.7);
opacity: 0.7;
}
100% {
-webkit-transform: scale(1);
transform: scale(1);
opacity: 1;
}
}
.animate__backInDown {
-webkit-animation-name: backInDown;
animation-name: backInDown;
-webkit-animation-duration: 1s;
animation-duration: 1s;
-webkit-animation-fill-mode: both;
animation-fill-mode: both;
}
</style>



