<template>
  <div class="hero-scroll-wrap section-color-gray">
    <div class="hero-scroll-trigger"></div>
      <div class="sticky-wrap sticky-wrap-hero">
        <div class="overflow-wrap">
          <header class="hero d-flex align-items-center">
            <img src="../assets/img/hero-bg-01.png" alt="Alexandre Jacquin développeur Web logo" class="hero-ui" />
            <div class="container container-hero d-flex flex-column justify-content-center align-items-center">
              <div class="hero-img-wrap">
                <div class="hero-img-container">
                  <img src="../assets/img/me.jpg" alt="citrus design logo" class="hero-img"/>
                </div>
                <div id="hero-img-shadow-1" class="hero-img-shadow-wrap"><div class="hero-img-shadow"></div></div>
                <div id="hero-img-shadow-2" class="hero-img-shadow-wrap hero-img-shadow-wrap-intro"><div class="hero-img-shadow"></div></div>
              </div>
              <div class="hero-header d-flex flex-column justify-content-center align-items-center">
                <h2 class="hero-eyebrow">Alexandre Jacquin</h2>
                <div class="hero-heading-ui-wrap">
                  <div class="hero-heading-ui">
                    <div class="hero-heading-ui-tab">
                      Made with Vuejs
                    </div>
                  </div>
                  <h1 class="hero-heading">Développeur Web Fullstack</h1>
                </div>
                <p class="hero-paragraph-mobile">Templates for ambitious companies.</p>
              </div>
            </div>
          </header>
        </div>
      </div>
    <div class="sticky-gap"></div>
  </div>
</template>

<script>
const $ = require('jquery')

export default {
  name: 'Header',
  mounted () {
    /* BACKGROUND HERO
    /*================ */
    const template = $(window)
    template.on('scroll', function () {
      const hero = $('.hero')
      const heroUi = $('.hero-ui')
      const heroHeadingUi = $('.hero-heading-ui')
      const scrollTrigger = $('.hero-scroll-trigger')
      const offset = scrollTrigger.offset()
      const windowTop = template.scrollTop()
      const scrollPercent = (offset.top - windowTop) / offset.top
      const scale = 'scale3D(' + scrollPercent + ', ' + scrollPercent + ', 1)'

      if (window.matchMedia('(min-width: 991px)').matches) {
        if (windowTop >= 981) {
          hero.css('transform', 'scale(.276014)')
        } else {
          hero.css('transform', scale)
        }
        if (windowTop > 50) {
          heroHeadingUi.css('opacity', '1')
          heroUi.css('opacity', '1')
        } else {
          heroHeadingUi.css('opacity', '0')
          heroUi.css('opacity', '0')
        }
      }
    })
  }
}
</script>

<style scoped>
</style>



